.About {
    text-align: center;
  }
  .About-header {
    background-image: url("./Album/Icarus.jpg");
    padding: 80px;
    background-position: center; /* Center the image */
    background-size: cover;
    background-blend-mode: lighten;
    font-family: freight-big-pro;
    font-weight: lighter;
    font-size: 28px;
    color: DarkOliveGreen;
  }
  
  .Sub-header {
    background-image: url("./Album/SAPortrait.jpg");
    background-clip: initial;
    padding: 195px;
    background-position: center; /* Center the image */
    background-size: contain;
    background-blend-mode: darken;
    font-family: freight-big-pro;
    font-weight: darker;
    font-size: 45px;
    color: rgb(157, 21, 53);
    text-align: left;
    text-justify: auto;
  }
  
  main {
    background-color: bisque;
    padding: 30px;
    font-family: Didot, "Bodoni MT", "Noto Serif Display", "URW Palladio L", P052,
      Sylfaen, serif;
    font-weight: lighter;
    font-size: 23px;
    line-height: 1.5;
    color: bisque;
    text-align: center;
  }
  
  .Links {
    padding: 45px;
    padding-bottom: 70px;
    background-color: darkolivegreen;
  }

  .Links h1 {
    padding-bottom: 40px;
    font-family: Didot, "Bodoni MT", "Noto Serif Display", "URW Palladio L", P052,
      Sylfaen, serif;
    font-weight: lighter;
    font-size: 22px;
    color: bisque;
  }

  html,
  body,
  body div,
  span,
  object,
  iframe,
  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  p,
  blockquote,
  pre,
  abbr,
  address,
  cite,
  code,
  del,
  dfn,
  em,
  img,
  ins,
  kbd,
  q,
  samp,
  small,
  strong,
  sub,
  sup,
  var,
  b,
  i,
  dl,
  dt,
  dd,
  ol,
  ul,
  li,
  fieldset,
  form,
  label,
  legend,
  table,
  caption,
  tbody,
  tfoot,
  thead,
  tr,
  th,
  td,
  article,
  aside,
  figure,
  footer,
  header,
  hgroup,
  menu,
  nav,
  section,
  time,
  mark,
  audio,
  video {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    font-size: 100%;
    vertical-align: baseline;
    background: transparent;
  }